<template>
    <div class="shopcar">
        <div class="box1"> 

        </div>
        <div class="box2" >
            
                <div class="box2_1">
                    <div class="center">
                        <p class="p1">购物车</p>
                        <p class="p2" @click="deletcount"> 清空</p>
                    </div>
                </div>
                <div class="box2_2" ref="wrapper">
                    <div class="box2_c">
                        <div class="center"  v-for="(item,k) in blcount" :key="k">
                            <div class="center_1">
                                    {{item.name}}
                            </div>
                            <tianjia :food='item' class="tianjia"></tianjia>    
                        </div>
                    </div>
                </div>
            </div>
    </div>
</template>

<script>
import tianjia from './tianjia'
import {mapGetters} from 'vuex'
import BScroll from 'better-scroll'

export default {
    computed:{
        ...mapGetters([
            'goods'
        ]),
        //遍历含有count的food
        blcount(){
            console.log(this.goods)
            let foods=[];
            // let goods = this.goods
            this.goods.forEach( good =>{
                good.foods.forEach( food =>{
                    if(food.count>0){
                       foods.push(food)
                    }
                })
            });
            return foods
        }
    },
    created(){
       
        setTimeout(()=>{
            // console.log('ccc'+this.goods)
        },3000)
    },
    mounted(){
        this.$nextTick( () => {
            setTimeout( () => {
                this._initmenu();
            },300)
        });
	},
    components:{

        tianjia
    },
    methods:{
        deletcount(){
            this.goods.forEach( good =>{
                good.foods.forEach( food =>{
                    food.count = 0
                })
            });
        },
         _initmenu(){
            this.menuScroll = new BScroll(this.$refs.wrapper,{'click':true});
        }
    }
}
</script>


<style lang="less" scoped>
.shopcar{
        position: absolute;
        width: 100%;
        // height: 100%;
        top: 0;
        // position: fixed;
        bottom: 0.90rem;
        background-color: rgba(7,17,27,0.6);
        z-index: 100;
        .box2{
            position: absolute;
            bottom: 0;
            width: 100%;
            height: 6.11rem;
            // .box2_c{

            // }
           
            .box2_1{
                width: 100%;
                height: 0.8rem;
                background-color:  #bdc1c0;
                margin: 0 auto;
                border-bottom: 0.01rem solid rgba(7,17,27,0.1);
                .center{
                    width: 95%;
                    height: 0.8rem;
                    // border: 1px solid #000;
                    margin: 0 auto;
                    display: flex;
                    justify-content:space-between;
                    
                    p{
                        height: 0.8rem;
                        line-height: 0.8rem;  
                    }
                    .p1{
                        font-size: 0.28rem;
                        font-weight: 200;
                        color: rgb(7,17,27)
                    }
                    .p2{
                        font-size: 0.24rem;
                        color: rgb(0,160,220)
                    }
                }
            }
            .box2_2{
                width: 100%;
                height: 5.27rem;
                background-color: #f3f5f7;
                margin: 0 auto;
                 overflow: hidden;
                .center{
                    width: 90%;
					height: 0.88rem;
					border-bottom: 0.01rem solid rgba(7,17,27,0.1);
                    margin: 0 auto;
					line-height: 0.88rem;
					font-size: 0.24rem;
					color: rgb(7, 17, 27);
                    font-weight: 200;
                    display: flex;
                    justify-content: space-between;
                    .center_1{
                        font-size: 0.28rem;
                        color: rgb(7, 17, 27);
                        
                    }
                    .tianjia{
                     margin-top: 0.16rem
                    }
                }
            }
        }
}
    
</style>



