<template>
    <div class="tianjia">
        <div class="jian" v-if="food.count" @click.stop.prevent="jian">-</div>
        <div class="num" v-if="food.count">{{food.count}}</div>
        <div class="jia" @click.stop.prevent="jia">+</div>
    </div>
</template>
<script>
import Vue from 'vue'
export default {
    props:['food'],
    methods:{
        jia(){
            if(this.food.count){
               this.food.count++
            }
            else{
                Vue.set(this.food,"count",1)
                console.log(this.food.count)
            }
            
        },
        jian(){
            this.food.count--;
        }
    }
    
}
</script>

<style lang="less" scoped>
.tianjia{
    display: flex;
}
    .jia{
        // position: absolute;
        // top: 0.9rem;
        // right: 1rem;
        background-color: rgb(0,160,220);
        border-radius: 0.3rem;
        width: 0.6rem;
        height: 0.6rem;
        line-height: 0.55rem;
        text-align: center;
        color: white;
        font-size: 0.5rem;
        font-weight: 500;
    }
    .jian{
        // position: absolute;
        // top: 0.9rem;
        // right: 2.3rem;
        background-color: white;
        border-radius: 0.4rem;
        border: 0.05rem solid rgb(0,160,220);
        width: 0.5rem;
        height: 0.5rem;
        line-height: 0.5rem;
        text-align: center;
        color: rgb(0,160,220);
        font-size: 0.5rem;
        font-weight: 500; 
        // display: none;
    }
    .num{
        //  position: absolute;
        // top: 0.9rem;
        // right: 1.65rem;
        // background-color: rgb(0,160,220);
        border-radius: 0.3rem;
        width: 0.6rem;
        height: 0.6rem;
        line-height: 0.6rem;
        text-align: center;
        color: white;
        font-size: 0.3rem;
        font-weight: 500; 
        color: black
    }
</style>



