<template>
	<transition enter-active-class="animated bounceInLeft" leave-active-class="animated bounceOutRight">
			
		<div class="detail">
			<div class="name">
				<p>{{seller.name}}</p>
			</div>
		<star :score='seller.score' class="star" :size = "'big'"></star>		
			<!-- <star :score = 'seller.score' :size = "'big'"></star> -->
				<div class="yh">
					<div class="b1"></div>
					<div class="b2">优惠信息</div>
					<div class="b3"></div>
					<div class="b4">
						<ul>
							<li>
								<img src="../../../static/imgs/decrease.png" >
								<span>{{seller.supports[0].description}}</span>
							</li>
							<li>
								<img src="../../../static/imgs/discount.png" >
								<span>{{seller.supports[2].description}}</span>
							</li>
							<li>
								<img src="../../../static/imgs/discount.png" >
								<span>{{seller.supports[1].description}}</span>
							</li>
							<li>
								<img src="../../../static/imgs/special.png" >
								<span>{{seller.supports[1].description}}</span>
							</li>
							<li>
								<img src="../../../static/imgs/special.png" >
								<span>{{seller.supports[2].description}}</span>
							</li>
						</ul>
					</div>
					<div class="b1 b"></div>
					<div class="b2 b">商家公告</div>
					<div class="b3 b"></div>
					<div class="text">
							{{seller.bulletin}}
					</div>
					<div class="x" @click="offDetail">x</div>
				</div>
		</div>

		
	</transition>
</template>

<script>
	import { mapGetters } from 'vuex';
	import star from './star.vue';
	import animate from 'animate.css'

	export default {
		computed:{
          ...mapGetters([
              'seller',
          ])     
	  },
	  methods:{
		  offDetail(){
			   this.$store.dispatch('offDetail')
		  }
	  },
		components:{
			star
		}
    }
</script>

<style lang="less" scoped>
	.detail{
		
		font-size: 0.4rem;
		position: absolute;
		top: 0;
		left: 0;
		right: 0;
		bottom: 0;
		background: black;
		opacity: 0.85;
		z-index: 1000;
		.name p{
			text-align: center;
			font-size: 0.32rem;
			font-weight: 700;
			color: rgb(255, 255, 255);
			line-height:0.32rem ;
			margin-top: 1.28rem; 
			margin-bottom: 0.32rem;
		}
		.star{
			text-align: center;	
			margin-left: 0.5rem;
		}
		.b1{
			width: 2.24rem;
			height: 0.1rem;
			border-bottom: 1px solid rgba(255,255,255,0.2);
			position: absolute;
			top: 2.96rem;
			left: 0.72rem;
		}
		.b3{
			width: 2.24rem;
			height: 0.1rem;
			border-bottom: 1px solid rgba(255,255,255,0.2);
			position: absolute;
			top: 2.96rem;
			right:0.72rem ;
		}
		.b2{
			font-size: 0.26rem;
			color: rgb(255, 255, 255);
			position: absolute;
			top: 2.96rem;
			left: 3.2rem;
		}
		.b4{
			
			color: rgb(255, 255, 255);
			position: absolute;
			top: 3.6rem;
			left: 0.96rem;
			img{
				width: 0.32rem;
				height: 0.32rem;
				margin-right: 0.12rem;
				vertical-align: middle;
			}
			ul {
				li{
					display: block;
					height: 0.24rem;
					font-size: 0.24rem;
					font-weight: 200;
					line-height: 0.24rem;
					margin-bottom: 0.24rem;
				}
			}	
		}
		.b{
			top: 6.2rem;
		}
		.text{
			width: 5.7rem;
			color: rgb(255, 255, 255);
			position: absolute;
			top: 6.68rem;
			left: 0.96rem;
			font-size: 0.24rem;
			font-weight: 200;
			color: rgb(255, 255, 255);
			line-height:0.48rem ;
		}
		.x{
			font-size: 0.64rem;
			color: rgba(255, 255, 255,0.5);
			position: absolute;
			bottom: 0.84rem;
			right: 49%;
			
		}
	}

</style>


