<template>
  <div id="app">
    <v-header class="header"></v-header>
        <div class="list"> 
          <div class="list-item" >
              <router-link to="/container">商品</router-link>
          </div>
          <div class="list-item">
              <router-link to="/ratings">评价</router-link>
          </div>
          <div class="list-item">
              <router-link to="/seller">商家</router-link>
          </div>
      </div>
      <v-detail v-if="flag"></v-detail>
      <v-partgoods v-if="showpgs" class="partgoods"></v-partgoods>
      <v-shopcar v-if="showcar"></v-shopcar>
      <keep-alive>
        <router-view></router-view>
      </keep-alive>
      <v-foot></v-foot>
  </div>
</template>

<script>
  import detail from './components/header/detail.vue'
  import shopcar from './components/header/shopcar.vue'
  import partgoods from './components/header/partgoods.vue'
  import header from '../src/components/header/header.vue'
  import foot from '../src/components/header/footer.vue'
  import {mapGetters} from 'vuex'
export default {
  computed:{
    ...mapGetters([
          'flag',
          "showpgs",
          'showcar'
    ])
  },

  created(){
    this.$store.dispatch('getSeller')
  },
  components:{
    'v-header' : header,
    'v-foot'   : foot,
    'v-partgoods':partgoods,
    'v-detail':detail,
    'v-shopcar' : shopcar
  }
}
</script>

<style lang="less" scoped>
  #app{
    .header{
      z-index:1;
    }
    .list{
        // border: 1px solid #000;
        font-size: 0.28rem;
        width: 100%;
        height: 0.8rem;
        line-height: 0.8rem;
        border-bottom:0.01rem solid rgba(7, 17,27,0.1);
        display: flex;
        position:relative;
        z-index:1;
        .list-item{
            flex:1;
            text-align: center;
        a{
          display: block;
          &.router-link-active{
            color:red;
          }
        }
      }
    }
  }
</style>



