import types from '../types.js'
import axios from 'axios'

var state = {
        ratings:[],
        allratetype:[],
        goodratetype:[],
        badratetype:[]
    }
    
    var actions = {
        getratings({ commit , state}){
            axios.get('./data.json').then((res)=>{
                commit(types.GET_RATINGS,res.data.ratings)
            })
        }
    }
    
    var getters = {
        ratings(state){
            return state.ratings

        }
    }
    
    var mutations = {
        [types.GET_RATINGS](state , data){
          console.log(data)
           state.ratings = data
           
        }
    }
    
    export default {
        state,
        actions,
        getters,
        mutations
    }


