<template>
<!-- header -->
    <div class="header"> 
            <div class="bg">
                <img :src="seller.avatar" >      
            </div>
            <div class="box1_1">
            </div>
            <div class="box1_3">
                <div class="bulletin">
                <img src="../../../static/imgs/bulletin.png" alt="">
                    <p>{{seller.bulletin}}</p>
                    <span>></span>
                </div>
            </div>
            <div class="box1_2">
                <img :src="seller.avatar" >        
            </div>
            <div class="brand">
                <img src="../../../static/imgs/brand.png" alt="">
            </div>
            <div class="name">
                <p>{{seller.name}}</p>
            </div>
            <div class="description">
                <p>
                    <span>{{seller.description}}</span>/
                    <span>{{seller.deliveryTime}}分钟送达</span>
                </p>
            </div>
            <div class="decrease">
                <img src="../../../static/imgs/decrease.png" alt="">
