                    <span>{{seller.supports[0].description}}</span>&nbsp;,
                    <span>满50减10</span>
                </p> 
                <div class="yuan" @click="showDetail">
                    <span v-if="seller.supports">{{seller.supports.length}}个&nbsp;&nbsp;></span>
                </div>     
            </div>         
            
    </div>


    
