<script>
    import { mapGetters} from 'vuex';
    
    export default {
      computed:{
          ...mapGetters([
              'seller',
          ])     
      },
      methods:{
              showDetail(){
                  console.log(445335667567)
                 
                  this.$store.dispatch('showDetail')
              }
          }
    }

</script>

<style scoped lang="less">
    .header{
        width: 100%;
        height: 2.68rem;
        position: relative;
        
            .bg{
                width: 100%;
                height: 2.68rem;
                position: absolute;
                top: 0;
                left: 0;
                right: 0;
                bottom: 0;
               
                filter: blur(5px);
                img{
                    width: 100%;
                    height: 100%;
                }
            }
            .box1_1{
                width: 100%;
                height: 2.12rem;
                position: absolute;
                top: 0;
                left: 0;
                right: 0;
                bottom: 0;
                background-color:#61676c;
                opacity: 0.6;
            }   
            .box1_3{
                position: absolute;
                bottom: 0;
                width: 100%;
                height: 0.56rem;
                background-color:#4d5358;
                opacity: 0.8;
                z-index: 100;
                .bulletin{
                    position: absolute;
                    width: 100%;
                    height: 0.56rem;
                    
                    line-height: 0.56rem;
                        img{
                        position: relative;
                        top: 0.05rem;
                        left: 0.24rem;
                        width: 0.4rem;
                        height: 0.25rem;
                        }
                        p{
                            white-space: nowrap;
                            overflow: hidden;
                            text-overflow: ellipsis;
                            position: absolute;
                            top: 0rem;
                            left:0.74rem;
                            width: 84.5%;
                            height: 0.56rem;
                            font-size: 0.2rem;
                            color: rgb(225, 225, 225);
                            font-weight: 100;
                        }
                        span{
                            position: absolute;
                            right: 0.24rem;
                            color: rgb(225, 225, 225);
                            font-weight: 100;
                        }
                    }
            }
            .box1_2{
                    width: 1.28rem;
                    height: 1.28rem;
                    img{
                        position: absolute;
                        width: 1.28rem;
                        height: 1.28rem;
                        padding-top: 0.48rem;
                        padding-left: 0.48rem;
                    }
                
                }
                .brand{
                    position: absolute;
                    left: 2.08rem;
                    top: 0.48rem;
                    img{
                        height: 0.36rem;
                    }

                }
                .name{
