                        width: 0.24rem;
                        height: 0.24rem;
                        position: absolute;
                        left: 2.08rem;
                        top: 1.4rem; 
                        }
                    p{
                        position: absolute;
                        left: 2.4rem;
                        top: 1.4rem;
                        font-size: 0.2rem;
                        color: rgb(225, 225, 225);
                        font-weight: 100;
                        line-height: 0.20rem;

                    }
                    .yuan{
                            position: absolute;
                            right: 0.24rem;
                            height:0.48rem ;
                            width: 1rem;
                            background-color: rgb(0,0,0,0,2);
                            border-radius: 0.16rem;
                            opacity: 0.4;
                        span{
                            position: relative;
                            left: 0.16rem; 
                            font-size: 0.2rem;
                            color: rgb(225, 225, 225);
                            font-weight: 100;
                            line-height: 0.5rem;
                            z-index: 100;
                        }
                    }
                    
                }
        }
      
        
    
   
</style>



