<template>
	<div class="seller" ref="wrapper">
		<div>
			<div class="Box1">
				<div class="box1">
					<p class="p1">{{seller.name}}</p>
					<div class="star">
							<star :score="seller.score"></star>
							<p>月售{{seller.sellCount}}单</p>
					</div>
				</div>
				<div class="box1 price">
					<div class="pr_1">
						<p class="pr_p1">起送价</p>
						<p class="pr_p2">
							<span class="sp1">{{seller.minPrice}}</span>
							<span class="sp2">元</span>
						</p>
					</div>
					<div class="pr_1">
						<p class="pr_p1">商家配送</p>
						<p class="pr_p2">
							<span class="sp1">{{seller.minPrice}}</span>
							<span class="sp2">元</span>
						</p>
					</div>
					<div class="pr_1">
						<p class="pr_p1">起送价</p>
						<p class="pr_p2">
							<span class="sp1">{{seller.deliveryTime}}</span>
							<span class="sp2">分钟</span>
						</p>
					</div>					
				</div>
			</div>
			<div class="bor"></div>
			<div class="Box2">
				<div class="box2_1">
					<p>公告与活动</p>
					<div class="box2_1_1">
						{{seller.bulletin}}
					</div>
				</div>
				<div class="box2_2">
					<ul>
						<li>
							<img src="../../../static/imgs/decrease.png" >
							<span v-if="seller.supports">{{seller.supports[0].description}}</span>
						</li>
						<li>
							<img src="../../../static/imgs/discount.png" >
							<span v-if="seller.supports">{{seller.supports[2].description}}</span>
						</li>
						<li>
							<img src="../../../static/imgs/discount.png" >
							<span v-if="seller.supports">{{seller.supports[1].description}}</span>
						</li>
						<li>
							<img src="../../../static/imgs/special.png" >
							<span v-if="seller.supports">{{seller.supports[1].description}}</span>
						</li>
						<li>
							<img src="../../../static/imgs/special.png" >
							<span v-if="seller.supports">{{seller.supports[2].description}}</span>
						</li>
					</ul>
				</div>
			</div>
			<div class="bor"></div>
			<div class="Box3 ">
				<div class="box3">
				商家实景
				</div>
				<div class="box1 xx" ref="ww">
					<div ref="aa">
						<img :src="item2" v-for="(item2 , k) in seller.pics" :key="k">
					</div>
				</div>
			</div>
			<div class="bor"></div>
			<div class="Box4 Box2">
				
				<div class="box4_1">
				商家信息
				</div>
				<div v-for="(item , i) in seller.infos " :key="i">
					<div class="box4_2">
						{{item}}
					</div>
				</div>
				
			</div>

		</div>
	</div>
</template>

<script>
import BScroll from 'better-scroll'
import {mapGetters} from 'vuex'
import star from './star.vue'

	export default {
		computed:{
			...mapGetters([
				'seller'
			])
		},
		components:{
			star
		},
		mounted(){
        this.$nextTick( () => {
            setTimeout( () => {
				this._initmenu();
            },300)
        });
	},
	 methods:{
        _initmenu(){
			this.menuScroll = new BScroll(this.$refs.wrapper,{'click':true});
			// this.imgScroll = new BScroll(this.$refs.ww,{'scrollX':true});
		},
		
	},
	watch:{
 			seller(){
 				let picWidth = 120;
	          	let margin = 6;
	          	let width = (picWidth + margin) * this.seller.pics.length - margin;
	          	this.$refs.aa.style.width = width + 'px';

 				if( !this.imgScroll ){
	 				this.imgScroll = new BScroll( this.$refs.ww , {
	 					scrollX: true,
	 				})
	 			}else{
	 				this.imgScroll.refresh();
	 			}
			 },
 		}
}
</script>

<style lang="less" scoped>
		.seller{
			width: 100%;
			position: fixed;
			top: 3.4rem;
			bottom: 1.2rem;
			overflow: hidden;
			.Box1{
				width: 100%;
				height: 2.4rem;
				// border: 0.01rem solid #000;
				.price{
					display: flex;
					.pr_1{
						width: 100%/3;
						height: 0.76rem;
						// border: 0.01rem solid #000;
						border-right: 0.01rem solid rgba(7,17,27,0.1);
						
						margin-top: 0.20rem;
						text-align: center;
						line-height: 0.36rem;
						.pr_p1{
							font-size: 0.2rem;
							color: rgb(147,153,159)
						}
						.pr_p2{
							margin-top: 0.08rem;
							.sp1{
								font-size: 0.48rem;
								color:rgb(7, 17, 27);
								font-weight: 200;
							}
							.sp2{
								font-size: 0.2rem;
								color: rgb(147,153,159)
							}
						}
					}
				}
				.box1{
				width: 95%;
				height: 1rem;
				// border: 0.01rem solid #000;
				// border-bottom: 0.01rem solid rgba(7,17,27,0.1);
				
				
				margin: 0 auto;
					.p1{
						font-size: 0.28rem;
						color: rgb(7, 17, 27);
						margin-top: 0.36rem;
						margin-bottom: 0.16rem;
					}
					.star{
						display: flex;
						P{
							font-size: 0.2rem;
							color: rgb(7, 17, 27);							
							line-height: 0.36rem;
							margin-left: 0.24rem;
						}
					}
				}
			}
			.bor{
				width: 100%;
				height: 0.32rem;
				// border: 0.01rem solid #000;
				background-color: rgba(147,153,159,0.2)				
			}
			.Box2{
				width: 100%;
				// height: 7.2rem;
				// border: 0.01rem solid #000;
				.box2_1{
						width: 95%;
						height: 3.2rem;
						// border: 0.01rem solid #000;
						border-bottom: 0.01rem solid rgba(7,17,27,0.1);
						
						margin: 0 auto;
					p{
						font-size: 0.28rem;
						color: rgb(7, 17, 27);
						margin-top: 0.36rem;
						margin-bottom: 0.1rem;
					}
					.box2_1_1{
						width: 90%;
						// border: 0.01rem solid #000;
						height: 2rem;
						margin: 0 auto;
						font-size: 0.24rem;
						font-weight: 200;
						color: rgb(240, 20, 20);
						line-height: 0.48rem;
						margin-top: 0.24rem;
					}
				
				}
				.box2_2{
				width: 95%;
				// height: 0.96rem;
				// border: 0.01rem solid #000;
				margin: 0 auto;
				img{
				width: 0.32rem;
				height: 0.32rem;
				margin-right: 0.12rem;
				vertical-align: middle;
			}
			ul {
				li{
					display: block;
					height: 0.96rem;
					font-size: 0.24rem;
					font-weight: 200;
					line-height: 0.96rem;
				// border: 0.01rem solid #000;
					border-bottom: 0.01rem solid rgba(7,17,27,0.1);
				
					
				}
			}	
				}
			}
			.Box3{
				width: 100%;
				// height: 2.4rem;
				.box3{
					width: 95%;
					height: 0.88rem;
					// border: 0.01rem solid #000;
					margin: 0 auto;
					line-height: 0.88rem;
					font-size: 0.28rem;
					color: rgb(7, 17, 27);	
				}
				.xx{
					white-space: nowrap;
					margin-bottom: 0.36rem;
					width: 95%;
					overflow: hidden;
					margin-left: 0.18rem;
					img{
						width: 2.4rem;
						height: 1.8rem;
						margin-right:0.12rem;
					}
				}
			}
			.Box4{
				// height: 4rem;
				overflow: hidden;
				.box4_1{
				width: 95%;
				height: 0.96rem;
				// border: 0.01rem solid #000;
				border-bottom: 0.01rem solid rgba(7,17,27,0.1);
				
				margin: 0 auto;
				line-height: 0.88rem;
				font-size: 0.28rem;
				color: rgb(7, 17, 27);
				}
				.box4_2{
					width: 90%;
					height: 0.88rem;
					border-bottom: 0.01rem solid rgba(7,17,27,0.1);
					margin: 0 auto;
					line-height: 0.88rem;
					font-size: 0.24rem;
					color: rgb(7, 17, 27);
					font-weight: 200;
				}
			}
			
		}
</style>


