<template>
    <div class="container">
        <div class="container-left wrapper" ref="wrapper">
            <ul>
                <li v-for="(item,i) in goods"  :key="i" @click='selectMenu(i)' class="cl-item" :class="{'current' : i===currentIndex}">
                    <span>{{item.name}}</span>
                </li>
            </ul>
        </div>
        <div class="container-right bter" ref="bter">
            <div>
                <div  v-for="(item,k) in goods" :key="k" class="Title foods-hook">

                    <div class="title">{{item.name}}</div>
                    <div class="title2" v-for="(item2,a) in item.foods" :key="a"  @click="showpgoods(item2)">
                        <ul>
                            <li>
                                <div class="box1">
                                    <img :src="item2.icon" alt="">
                                </div>
                                <div class="box2">
                                    <div class="box2_1">{{item2.name}}</div>
                                    <div class="box2_2">{{item2.description}}</div>
                                    <div class="box2_3">月售{{item2.sellCount}}&nbsp;&nbsp;好评率{{item2.rating}}</div>
                                    <div class="box2_4">¥{{item2.oldPrice}}&nbsp;&nbsp;{{item2.price}}
                                    </div>
                                    <tianjia :food="item2" class="tianjia"></tianjia>
                                </div> 
                            </li>
                        </ul>
                    </div>
                </div>
            </div>
            
        </div>
    </div>
</template>

<script>

import tianjia from './tianjia.vue'
import {mapGetters} from 'vuex'
import BScroll from 'better-scroll'
export default {
    components:{
        tianjia,
    },
    data(){
        return{
            listHeigt : [],
            scrollY: 0 
        }
    },
    computed:{
        ...mapGetters([
            'goods',
            'showpgs'
        ]),
        
        //计算当前索引值
        currentIndex(){
            for( let i = 0; i<this.listHeigt.length ; i++){
                if( this.scrollY < this.listHeigt[i+1] - 1){
                    return i;
                }
            }
            return 0
        },
        
    },
    created(){
        this.$store.dispatch('getgoods');
        this.$nextTick( () => {
            setTimeout( () => {
                this.wrapper();
                this.doHeight()
            },100)
        });
    },
   
    methods:{ 
        wrapper(){
            this.menuScroll = new BScroll(this.$refs.wrapper,{'click':true});
            this.menuScroll2 = new BScroll(this.$refs.bter,{
                'click':true,
                // 作用：有时候我们需要知道滚动的位置。
					// 为 1 的时候，会非实时（屏幕滑动超过一定时间后）派发scroll 事件；当 probeType 
					// 为 2 的时候，会在屏幕滑动的过程中实时的派发 scroll 事件；当 probeType 
					// 为 3 的时候，不仅在屏幕滑动的过程中，而且在 momentum 滚动动画运行过程中实时派发 scroll 	事件。如果没有设置该值，其默认值为 0，即不派发 scroll 事件。
                probeType:3
            });
            //实时获取y坐标
            this.menuScroll2.on( 'scroll' ,(pos) =>{
                this.scrollY =  Math.abs( Math.round(pos.y));
            })
        },
       showpgoods(item2){
            // console.log(444234)
                  this.$store.dispatch('showpgoods',item2)
              },
              //计算每组类的高度 并且push进listHeight数组当中
        doHeight(){
            //获取div
           let fooddiv = this.$refs.bter.getElementsByClassName('foods-hook');
           //0初始值
           let height = 0;
           this.listHeigt.push(height);
           for(let i=0 ; i < fooddiv.length; i++ ){
               height += fooddiv[i].clientHeight;
                this.listHeigt.push(height);
           }
            // console.log(this.listHeigt)
        },
        // 左侧点击事件
			selectMenu(i){
				console.log(i);
				let fooddiv = this.$refs.bter.getElementsByClassName('foods-hook');
				let el = fooddiv[i];
				this.menuScroll2.scrollToElement(el,300);
			}
        
    }
  
}
</script>

<style lang="less" scoped>
        .container{
            width: 100%;
            position: absolute;
            top: 3.48rem;
            bottom: 0.92rem;
            // border: 1px solid #000;
            overflow: hidden;
            display:flex;
            z-index: -3;
            .container-left{
                width: 1.6rem;
                // height: 5rem;
                // border: 1px solid #000;
                background-color: #f3f5f7;
                // display: flex;
                flex: 0 0 1.6rem;
                z-index: -2;
                
                ul{
                    li{
                        width: 1.6rem;
                        height: 1.08rem;
                        display: table;
                        line-height: 0.4rem;
                        &.current{
                            color:red;
                            background-color: white
				            }
                        span{
                            display: table-cell;
                            width: 1.12rem;
                            font-size: 0.24rem;
                            font-weight: 200;
                            text-align: center;
                            vertical-align: middle;
                            border-bottom: 1px solid rgba(7, 17, 27, 0.1);
                            
                        }
                    }
                }
                
            }
            .container-right{
                // width: 100%-1.6rem;
                height: 100%;
                flex:1;
                z-index: -2;
                // border: 1px solid #000;
                .title{
                    height: 0.52rem;
                    // border: 1px solid #000; 
                    line-height: 0.52rem;
                    padding-left: 0.28rem;
                    color: rgb(147, 153, 159);
                    font-size: 0.24rem;
                    border-left: 0.1rem solid #d9dde1;
                    background-color: #f3f5f7;    
                }
                .title2{
                    height: 2.2rem; 
                     border-bottom: 1px solid rgba(7, 17, 27, 0.1);
                    img{
                        width: 1.12rem;
                    }
                    li{
                        display: flex;
                        .box1{
                            position: relative;
                            top:0.36rem;
                            left: 0.36rem;
                        }
                       
                        .box2{
                            height:1.5rem ;
                            position: relative;
                            top:0.36rem;
                            left: 0.5rem;
                            .tianjia{
                                position: absolute;
                                top: 1rem;
                                right: 1rem;
                
                            }
                            .box2_1{
                                font-size: 0.28rem;
                                height: 0.28rem ;
                                color: rgb(7, 17, 27);
                                line-height: 0.28rem;
                                margin-bottom: 0.16rem;
                            }
                            .box2_2{
                                font-size: 0.2rem;
                                // height: 0.28rem ;
                                color: rgb(147, 153, 159);
                                line-height: 0.28rem;
                                margin-bottom: 0.16rem;
                            }
                            .box2_3{
                                font-size: 0.2rem;
                                height: 0.28rem ;
                                color: rgb(147, 153, 159);
                                line-height: 0.28rem;
                                margin-bottom: 0.16rem;
                            }
                            .box2_4{
                                font-size: 0.28rem;
                                height: 0.28rem ;
                                color: rgb(219, 41, 41);
                                line-height: 0.28rem;
                                width: 5rem;
                                display: flex;
                                 
                            }
                        }
                    }
                }
            }
        }
</style>



