import Vue from 'vue'
import Router from 'vue-router'
// import HelloWorld from '@/components/HelloWorld'
import Container from '../components/header/container.vue'
import Seller from '../components/header/seller.vue'
import Ratings from '../components/header/ratings.vue'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path:'/container',
      component:Container
    },
    {
      path:'/seller',
      component:Seller
    },
    {
      path:'/ratings',
      component:Ratings
    },
    {
      path:"*",
      component:Container,
      redirect:"/container"
    }
  ]
})



