import types from '../types.js'
import axios from 'axios'

    var state = {
        seller:{},
        flag:false
    }
    
    var actions = {
        getSeller({ commit, state}){
            axios.get('./data.json').then( (res)=>{

                    commit(types.GET_SELLER ,res.data.seller);
            })
        },
        showDetail({ commit }){
            console.log(11),
            commit(types.SHOW_DETAIL)
        },
        offDetail({ commit }){
            commit(types.OFF_DETAIL)
        }
    }
    
    var getters = {
        seller(state){
            return state.seller
        },
        flag(state){
            return state.flag
        }
    }
    
    var mutations = {
        [types.GET_SELLER](stated,data){
            state.seller = data
        },
        [types.SHOW_DETAIL](state,data){
            state.flag = true 
            console.log(state.flag)
        },
        [types.OFF_DETAIL](state,data){
            state.flag = false 
            console.log(state.flag)
        }
    }
    
    export default {
        state,
        actions,
        getters,
        mutations
    }


