<template>
    <div class="footer">
       <div class="yuan1"  ></div>
       <div class="yuan2" :class="{'have2' : totalcount > 0}" ></div>
       <div class="car" @click="showcar">
           <img src="../../../static/imgs/gshopcar.png" id="im">
       </div>
       <div class="count" v-if="!totalcount == 0">{{totalcount}}</div>
       <div class="text">
           <!-- {{totalcount}} -->
           <div class="text1">¥{{totalprice}}</div>
           <div class="text2">另需配送费¥{{seller.deliveryPrice}}</div>
           <div class="text3" :class="{'have' : totalprice >= seller.minPrice}">{{pay}}</div>
       </div>
    </div>
</template>


<script>
  import {mapGetters} from 'vuex'
//   import {mapGetters} from 'vuex'


export default {
    computed:{
        ...mapGetters([
            'seller',
            'goods'
        ]),
         //筛选所有有count的商品
        selectfoods(){
            let foods = [];
            this.goods.forEach(good =>{
                good.foods.forEach( food =>{
                    if( food.count>0){
                        foods.push(food);
                    }
                })
            });
            return foods;
        },
        //计算购物车商品总数
        totalcount(){
            let count = 0;
            this.selectfoods.forEach(food =>{
                count+= food.count;
                
            });
            return count;
        },
        //计算总价
        totalprice(){
            let price =0;
            this.selectfoods.forEach( food =>{
                price+=food.count * food.price
                if(this.totalcount > 0){
                    document.getElementById("im").src="../../../static/imgs/wshopcar.png";
                    console.log(1234566)
                }
            });
            return price
        },
        //结算
        pay(){
            if( this.totalprice ===0 ){
                return `￥ ${this.seller.minPrice}`
            }else if(this.totalprice < this.seller.minPrice){
                return `还差 ￥ ${this.seller.minPrice - this.totalprice}`
            }else{
                return '去结算'
            }
        }
      
    },
    methods:{
        showcar(){
            // alert(2)
            this.$store.dispatch('showcar')
        }
    },
    //  computed:{
    //     //筛选所有有count的商品
        
  
    // }
   
}
</script>

<style lang="less" scoped>
    .footer{
        width: 100%;
        height: 0.92rem;
        background-color: #141d24;
         opacity: 0.95;
        // border: 1px solid red;
        position: absolute;
        bottom: 0;
        z-index: 100;
        .yuan1{
            width: 1.12rem;
            height: 1.12rem;
            border-radius: 0.56rem;
            background-color: #141d24;
            opacity: 0.95;
            position: absolute;
            bottom: 0.04rem;
            left: 0.24rem;
            
        }
        .yuan2{
            width: 0.88rem;
            height: 0.88rem;
            border-radius: 0.44rem;
            background-color: rgba(255, 255, 255, 0.2);
            position: absolute;
            bottom: 0.16rem;
            left: 0.36rem;
            z-index: 0;
            &.have2{
            background-color: rgb(0,160,220);
                
            }
            
        }
        .car{
                width: 0.5rem;
                height: 0.5rem;
                position: absolute;
                left: 0.55rem;
                top: 0.1rem;
                img{
                    z-index: 1000;

                    width: 0.5rem;
                    height: 0.5rem;
                    
                }
            }
            .count{
                width: 0.4rem;
                height: 0.4rem;
                font-size: 0.24rem;
                // border: 1px solid #000;
                border-radius: 0.2rem;
                background-color: rgb(0,160,220);
                z-index: 100000;
                position: absolute;
                left: 1.1rem;
                top: -0.2rem;
                text-align: center;
                color: white;
                line-height: 0.4rem;
            }
        .text{
            font-size: 0.32rem;
            color: rgba(255, 255, 255,0.4);
            display: flex;
            position: absolute;
            left: 1.6rem;
            .text1{
                width: 0.6rem;
                height: 0.92rem;
                line-height: 0.92rem;
                // margin-top: 0.2rem;
                margin-right: 0.24rem;
                border-right: 0.01rem solid rgba(255, 255, 255, 0.1)
            }
            .text2{
                 height: 0.92rem;
                line-height: 0.92rem;
                //  margin-top: 0.2rem;
            }
            .text3{
                width: 2.1rem;
                height: 0.92rem;
                line-height: 0.92rem;
                text-align: center;
                background-color: rgba(255, 255, 255, 0.2);
                position: absolute;
                right:-3.1rem;
                 &.have{
                 background-color: rgb(0,160,220);
                 color: white;
                
            }
            }

        }
    }
    
</style>



