<template>
	<div class="ratings" ref="cc">
		<div>
			<div class="box1">
				<div class="box1_1">
					<p class="p1">{{seller.score}}</p>
					<p class="p2">综合评分</p>
					<p class="p3">高于周边商家{{seller.rankRate}}%</p>
				</div>
				<div class="box1_2">
					<div class="test">
						<div class="fw">服务态度</div>	
						<star :score='seller.score' class="star"></star>
						<span>{{seller.score}}</span>
					</div>
					<div class="test t2">
						<div class="fw">服务态度</div>	
						<star :score='seller.serviceScore' class="star"></star>
						<span>{{seller.serviceScore}}</span>
					</div>
					<div class="test t2">
						<div class="fw">送达时间</div>	
						<div class="time">{{seller.deliveryTime}}分钟</div>
					</div>
				</div>
			</div>
			<div class="border"></div>
			<div class="box5_1">
				<div class="cent">
					<div class="pj_1" @click="all">
						全部
						<span class="sp1">{{ratings.length}}</span>
					</div>
					<div class="pj_2" @click="good">
						推荐
						<span class="sp2">{{selectGrating.length}}</span>
					</div>
					<div class="pj_3" @click="bad">
						吐槽
						<span class="sp3">{{selectBrating.length}}</span>
					</div>
				</div>	
			</div>
				<div class="gou" @click="content">
					<img src="../../../static/imgs/钩.png" alt="">
					<span>只看有内容的评价</span>
				</div>

			<div class="pl" v-for="(item , k) in xxx" :key="k" v-if="flag || item.text">
					<div class="left">
						<img :src="item.avatar" alt="">
					</div>
					<div class="right">
						<div class="timeline">
							<p class="pn">{{item.username}}</p>
							<p class="pt">{{item.rateTime}}</p>
						</div>
						<div class="stline">
							<star :score='seller.score' class="star" :size="'small'"></star>
							<p>{{item.deliveryTime}}分钟送达</p>
						</div>
						<div class="text">{{item.text}}</div>
						<div class="tuijian">
							<div>
								<img src="../../../static/imgs/zan.png"  v-if="item.rateType==0">
								<img src="../../../static/imgs/cha.png" v-if="item.rateType==1">
							</div>
							<div v-for="(item2 ,i ) in item.recommend " :key="i" class="cented">
									<div class="zan">{{item2}}</div>
							</div>
							
						</div>
					</div>
				</div>
		</div>
	</div>
</template>

<script>
import {mapGetters} from 'vuex'
import star from './star.vue'
import BScroll from 'better-scroll'

	export default {
		computed:{
			...mapGetters([
				'ratings',
				'seller'
			]),
			//筛选推荐的评论
			selectGrating(){
				this.xxx = this.ratings
				let goodratetypes = []
				this.ratings.forEach( goodratetype =>{
					if(goodratetype.rateType == 0){
						goodratetypes.push(goodratetype)
					}
				});
				return goodratetypes

			},
			//筛选吐槽的评论
			
			selectBrating(){
				this.xxx = this.ratings
				let badratetypes = []
				this.ratings.forEach( badratetype =>{
					if(badratetype.rateType == 1){
						badratetypes.push(badratetype)
						// console.log(111111)
						// console.log(badratetype)
					}
				});
				return badratetypes
			},
			//全部评论
			// allrating(){
			// 	this.ratings.forEach(allrating =>{
			// 		if(allrating.rateType == 1){
			// 			document.getElementById('img').src='../../../static/imgs/cha.png'
			// 		}else{
			// 			document.getElementById('img').src='../../../static/imgs/zan.png'
			// 		}
			// 	})
			// }
		},
		created(){
			this.$store.dispatch('getratings');
		},
		components:{
			star
		},
		data:function(){
			return {
				xxx:[],
				"flag":true
			}
		},
		watch:{
			ratings(){
				setTimeout(()=>{
					if( !this.menuScroll){
						this._initmenu()
					}else{
						this.menuScroll.refresh();
					}
				},300)
			}
		},
	// 	mounted(){
    //     this.$nextTick( () => {
    //         setTimeout( () => {
    //             this._initmenu();
    //         },300)
    //     });
	// },
	 methods:{
        _initmenu(){
            this.menuScroll = new BScroll(this.$refs.cc,{'click':true});
		},
		all(){
			this.xxx = this.ratings;
			console.log(this.xxx)
			this.menuScroll.refresh();
			
		},
		good(){
			this.xxx = this.selectGrating;
			console.log(this.xxx)
			this.menuScroll.refresh();
			
		},
		bad(){
			this.xxx = this.selectBrating;
			this.menuScroll.refresh();
			
		},
		content(){
			console.log(111);
			this.flag = !this.flag;
			this.menuScroll.refresh();
			
		}	
    }
}
</script>

<style lang="less" scoped>
		.ratings{
			position: fixed;
			top: 3.4rem;
			bottom: 1.2rem;
			overflow: hidden;
			.box1{
				width: 100%;
				height: 2.04rem;
				// border: 1px solid #000;
				display: flex;
				.box1_1{
					width: 2.75rem;
					height: 1.3rem;
					// border: 1px solid #000;
					margin-top: 0.36rem;
					text-align: center;	
					line-height: 0.3rem;
					.p1{
						font-size: 0.48rem;
						color: rgb(225, 153, 0);
						line-height: 0.56rem;
					}				
					.p2{
						font-size: 0.24rem;
						color: rgb(7, 17, 27);
						line-height: 0.4rem;
					}
					.p3{
						font-size: 0.2rem;
						color: rgb(147,153,159);
						line-height: 0.4rem;
					}
				}
				.box1_2{
					width: 4.74rem;
					height: 1.3rem;
					// border: 1px solid #000;
					margin-top: 0.36rem;
					text-align: center;	
					line-height: 0.3rem;
					.star{
						font-size:0.24rem;
						color: rgb(225, 153, 0);	
					}
					.test{
						display: flex;
						margin-left: 0.48rem;
						.fw{
							font-size: 0.24rem;
							color: rgb(7, 17, 27);
							height: 0.36rem;
							line-height: 0.36rem;
							margin-right: 0.2rem;
						}
						.time{
							height: 0.36rem;
							line-height: 0.36rem;
							color: rgb(147,153,159);
						}
						span{
							color: rgb(225, 153, 0);
							line-height: 0.36rem;
						}
					}
					.t2{
						margin-top: 0.1rem;
					}
				}
			}
			.border{
				width: 100%;
				height: 0.36rem;
				// border: 1px solid #000;		
				background-color:  rgba(147,153,159,0.3);
			}
			.box5_1{
                
                margin-left: 0.36rem;
				margin-top: 0.36rem;
				.cent{
				display: flex;
				width: 95%;
				height: 1.28rem;
				border-bottom: 0.01rem solid rgba(147,153,159,0.3);
                .pj_1{
                    width: 1rem;
                    height: 0.56rem;
                    // border: 1px solid black;
                    margin-right: 0.16rem;
                    text-align: center;
                    line-height: 0.56rem;
                    background-color: rgb(0,160,220);
                    color: rgb(255,255,255); 
                    border-radius: 0.02rem;
                    .sp1{
                      font-size: 0.12rem;
                      color: rgb(255,255,255);  
                    };
               
                }
                .pj_2{
                    width: 1rem;
                    height: 0.56rem;
                    // border: 1px solid black;
                    margin-right: 0.16rem;
                    text-align: center;
                    line-height: 0.56rem;
                    background-color: rgba(0,160,220,0.4);
                    color: rgb(77,85,93);  
                    border-radius: 0.02rem;
					
                    .sp2{
                        font-size: 0.14rem;
                    }
                    
                }
                .pj_3{
                    width: 1rem;
                    height: 0.56rem;
                    // border: 1px solid black;
                    text-align: center;
                    line-height: 0.56rem;
                    background-color: rgba(77,85,93,0.2);
                    color: rgb(77,85,93); 
                    border-radius: 0.02rem;
                    .sp3{
                        font-size: 0.14rem;
                    }
				}	 
			}
		}
		 .gou{
                padding-left: 0.36rem;
                margin-top: 0.24rem;
                font-size: 0.24rem;
				color: rgb(147, 153, 159);
				width: 100%;
				height: 0.78rem;
					// border: 1px solid black;
				border-bottom: 0.01rem solid rgba(7, 17, 27, 0.1);
                img{
                    width: 0.54rem;
                    height: 0.54rem;
                    vertical-align: middle;
                }
			}
			.pl{
				width: 90%;
				height: 2.5rem;
				// border: 1px solid black;
				margin-left: 0.36rem;
				display: flex;
				margin-top: 0.36rem;
				.left{
					width: 0.56rem;
					height: 2.5rem;
					// border: 1px solid black;
					img{
						width: 0.55rem;
						height: 0.55rem;
						border-radius: 0.27rem;
					}
				}
				.right{
					width: 6.5rem;
					margin-left: 0.24rem;
					.timeline
					{
						display: flex;
						.pn{
							margin-right: 3.4rem;
							font-size: 0.2rem;
							color: rgb(7, 17, 27);
							line-height: 0.24rem;
						}
						.pt{
							font-size: 0.2rem;
							font-weight: 200;
							color: rgb(147, 153, 159);
							line-height: 0.24rem;
						}
					}
					.stline{
						display: flex;
						p{
							height: 0.36rem;
							line-height: 0.46rem;
							font-size: 0.2rem;
							font-weight: 200;
							color: rgb(147, 153, 159);
						}
					}
					.text{
						width: 5.9rem;
						font-size: 0.24rem;
						color: rgb(7, 17, 27);
						line-height: 0.36rem;
						margin-top: 0.16rem;
					}
					.tuijian{
						display: flex;
						width: 5.9rem;
						overflow: hidden;
						margin-top: 0.16rem;
						img{
							width: 0.26rem;
							
						}
						.cented{
								float: left;	
								// width: 2.5rem;
								overflow: wrap; 
							.zan{
								width: 1.2rem;
								height: 0.42rem;
								line-height: 0.42rem;
								text-align: center;
								font-size: 0.18rem;
								color: rgb(147,153,159);
								border: 0.01rem solid rgba(7, 17, 27, 0.1);
								border-radius: 0.02rem;
								overflow: hidden;
								margin-right: 0.16rem;
								margin-left: 0.16rem;
								
							}
						}
	
					}
				}
			}
	}
</style>


