<template>
	<transition enter-active-class="animated zoomIn" leave-active-class="animated zoomOut">
        <div class="partgoods wrapper" ref="cc" v-if="showpgs">
            <div>
                <div class="box1">
                    <img :src="food.image" @click="closepgoods">
                </div>
                <div class="box2">
                    <div class="name"> {{food.name}}</div>
                    <div class="sellcount">
                        <span>月售{{food.sellCount}}份</span>
                        <span>好评率{{food.rating}}％</span>
                    </div>
                    <div class="price">
                        <span>¥{{food.price}}</span>
                        <!-- <span>¥{{food.oldPrice}}</span> -->
                    </div>
                   
                    <tianjia :food="food"  v-show="food.count" class="tianjia"></tianjia>
                 
                    <div class="scar" @click="showtj" v-show="!food.count">加入购物车</div> 
                </div>
                <div class="box3"></div>
                <div class="box4">
                    <h4>商品介绍</h4>
                    <p>{{food.info}}</p>
                </div>
                <div class="box3"></div>
                <div class="box5">
                    <h4>商品评价</h4>
                    <div class="box5_1">
                        <div class="pj_1" @click="all">
                            全部
                            <span class="sp1">{{food.ratings.length}}</span>
                        </div>
                        <div class="pj_2" @click="good">
                            推荐
                            <span class="sp2">{{selectGrating.length}}</span>
                        </div>
                        <div class="pj_3" @click="bad">
                            吐槽
                            <span class="sp3">{{selectBrating.length}}</span>
                        </div>
                    </div>
                    <div class="bor"></div>
                    <div class="gou">
                            <img src="../../../static/imgs/钩.png" alt="">
                            <span>只看有内容的评价</span>
                    </div>
                    <div class="allratings">
                        <div class="center" v-for="(item,k) in xxx " :key="k" >
                            <div class="center_1">
                                <p class="p1">
                                    <!-- {{food.ratings}} -->{{item.rateTime}}
                                </p>
                                <p class="p2">
                                    <img src="../../../static/imgs/cha.png" alt="">
                                    <span>{{item.text}}</span>
                                </p>
                            </div>
                            <div class="center_2">
                                <span>{{item.username}}</span>
                                <span><img :src="item.avatar" alt=""></span>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </transition>
</template>


<script>
import Vue from 'vue'
import tianjia from './tianjia.vue'
import animate from 'animate.css'
import { mapGetters} from 'vuex';
import BScroll from 'better-scroll'

export default {
    data(){
        return{
            xxx:[]
        }
    },
    created(){
        this.xxx = this.food.ratings
    },
    computed:{
        ...mapGetters([
            'food',
            'showpgs'
        ]),
        //筛选推荐的评论
			selectGrating(){
				let goodratetypes = []
				this.food.ratings.forEach( goodratetype =>{
					if(goodratetype.rateType == 0){
						goodratetypes.push(goodratetype)
					}
				});
				return goodratetypes

			},
			//筛选吐槽的评论
			selectBrating(){
				let badratetypes = []
				this.food.ratings.forEach( badratetype =>{
					if(badratetype.rateType == 1){
						badratetypes.push(badratetype)
						// console.log(111111)
						
						// console.log(badratetype)
					}
				});
				return badratetypes
			},

    },
   components:{
        tianjia,
    },
    mounted(){
        this.$nextTick( () => {
            setTimeout( () => {
                this._initmenu();
            },300)
        });
    },
    // watch : {
    //     food:{  
    //             handler:function(val,oldval){  
    //                 console.log(val)  
    //             },  
    //             deep:true//对象内部的属性监听，也叫深度监听  
    //     },  
    //     // food(){
    //     //     console.log(55)
    //     //      this.$nextTick( () => {
    //     //         setTimeout( () => {
    //     //             this._initmenu();
    //     //         },1000)
    //     //     });
    //     // }
    // },
    methods:{
        _initmenu(){
            this.menuScroll = new BScroll(this.$refs.cc,{'click':true});
        },
        showtj(){
            Vue.set(this.food,'count',1)
        },
        all(){
			this.xxx = this.food.ratings;
			console.log(this.xxx)
		},
		good(){
			this.xxx = this.selectGrating;
			console.log(this.xxx)
		},
		bad(){
			this.xxx = this.selectBrating;
			
        },
        closepgoods(){
             this.$store.dispatch('closepgoods')

        }
    }
}
</script>


<style lang="less" scoped>
    .partgoods{
        position: fixed;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0.95rem;
        z-index:100;
        background-color:white;
        .box1{
            width: 100%;
            height: 7.5rem;
            // border: 1px solid black;
            img{
            width: 100%;
            height: 7.5rem;
            }
        }
        .box2{
            width: 100%;
            height: 2.2rem;
            background-color: white;
            border-bottom: 0.01rem solid rgba(7, 17, 27, 0.1);
            .tianjia{
                position: absolute;
                top: 8.7rem;
                right: 0.5rem;
               
               
            }
            .name{
                height: 0.28rem;
                line-height: 0.28rem;
                font-size: 0.28rem;
                font-weight: 700;
                color: rgb(7,17,27);
                padding-top: 0.36rem;
                margin-left: 0.36rem;
            }
            .sellcount{
                font-size: 0.2rem;
                color: rgb(147, 153, 159);
                margin-top: 0.16rem;
                margin-left: 0.36rem;
                span{
                    margin-right: 0.24rem;
                }
            }
            .price{
                color: rgb(240,20,20);
                font-size: 0.28rem;
                font-weight: 700;
                line-height: 0.48rem;
                height: 0.48rem;
                margin-left: 0.36rem;
                margin-top: 0.36rem;
            }
            .scar{
                width: 1.48rem;
                height: 0.48rem;
                line-height: 0.48rem;
                border-radius: 0.24rem;
                font-size: 0.2rem;
                color: rgb(255, 255, 255);
                background-color: rgb(0,160,220);
                text-align: center;
                position: relative;
                left: 5.6rem;
                bottom: 0.56rem;
            }
        }
        .box3{
            width: 100%;
            height: 0.32rem;
            background-color: #f3f5f7
        }
        .box4{
            width: 100%;
            height: 2.2rem;
            background-color: white;
            border-bottom: 0.01rem solid rgba(7, 17, 27, 0.1);
            h4{
                padding-top: 0.36rem;
                margin-left: 0.36rem;
            }
            p{
                width: 85%;
                margin-left: 0.52rem;
                font-size: 0.24rem;
                font-weight: 200;
                color: rgb(147, 153, 159);
                line-height: 0.48rem;
            }
        }
        .box5{
            width: 100%;
            // height: 2.6rem;
            background-color: white;
            border-bottom: 0.01rem solid rgba(7, 17, 27, 0.1);
            h4{
                padding-top: 0.36rem;
                margin-left: 0.36rem;
            }
            .box5_1{
                display: flex;
                margin-left: 0.36rem;
                margin-top: 0.36rem;
                .pj_1{
                    width: 1rem;
                    height: 0.56rem;
                    // border: 1px solid black;
                    margin-right: 0.16rem;
                    text-align: center;
                    line-height: 0.56rem;
                    background-color: rgb(0,160,220);
                    color: rgb(255,255,255); 
                    border-radius: 0.02rem;
                    .sp1{
                      font-size: 0.12rem;
                      color: rgb(255,255,255);  
                    };
               
                }
                .pj_2{
                    width: 1rem;
                    height: 0.56rem;
                    // border: 1px solid black;
                    margin-right: 0.16rem;
                    text-align: center;
                    line-height: 0.56rem;
                    background-color: rgba(0,160,220,0.4);
                    color: rgb(77,85,93);  
                    border-radius: 0.02rem;
                  
                    .sp2{
                        font-size: 0.14rem;
                    }
                    
                }
                .pj_3{
                    width: 1rem;
                    height: 0.56rem;
                    // border: 1px solid black;
                    text-align: center;
                    line-height: 0.56rem;
                    background-color: rgba(77,85,93,0.2);
                    color: rgb(77,85,93); 
                    border-radius: 0.02rem;
                    
                    .sp3{
                        font-size: 0.14rem;
                    }
                }
            }
            .bor{
                width: 85%;
                margin-left: 0.42rem;
                margin-top: 0.32rem;
                border: 0.01rem solid rgba(7,17,27,0.1);
            }
            .gou{
                margin-left: 0.36rem;
                margin-top: 0.24rem;
                font-size: 0.24rem;
                color: rgb(147, 153, 159);
                img{
                    width: 0.48rem;
                    height: 0.48rem;
                    vertical-align: middle;
                }
            }
            .allratings{
                width: 100%;
                // height: 2.6rem;
                background-color: white;
                
                .center{
                    padding-top: 0.36rem;
                    width: 85%;
                    height: 1.2rem;
                    margin-left: 0.42rem;
                    border-bottom: 0.01rem solid rgba(7,17,27,0.1);
                    // border: 0.01rem solid rgba(7,17,27,0.1);
                    // border: 0.01rem solid black;
                    display: flex;
                    justify-content: space-between ;
                    .center_1{
                            margin-top: 0.05rem;
                            .p1{
                                font-size: 0.2rem;
                                color:rgb(147, 153, 159);
                                height: 0.24rem;
                                line-height: 0.24rem;
                                padding-bottom: 0.12rem;
                            }
                            .p2{
                                font-size: 0.24rem;
                                color: rgb(7,17,27);
                                height: 0.32rem;
                                line-height: 0.32rem;
                                img{
                                     width: 0.24rem;
                                }
                            } 
                    }
                    .center_2{
                        margin-left: 3.5rem;
                        width: 2.4rem;
                        height: 0.3rem;
                        // border: 0.01rem solid black;
                        line-height: 0.3rem;
                        vertical-align: middle;
                        color:rgb(147, 153, 159);
                        img{
                            // margin-left: 1rem;
                            width: 0.24rem;
                            height: 0.24rem;
                            border-radius: 0.12rem;     
                        }
                    }
                }
            }
            
        }
    }
</style>



