import types from '../types.js';
import axios from 'axios'

var state = {
    goods:[],
    showpgs:false,
    food :{},
    showcar:false,
    
}

var actions = {
    getgoods({ commit , state}){
        // console.log('ss')
        axios.get('./data.json').then((res)=>{
                commit(types.GET_GOODS,res.data.goods)
        })
    },
    showpgoods({commit,state},item2){ 
        commit(types.SHOW_PARTGOODS,item2)
    },
    closepgoods({commit, state}){
        commit(types.CLOSE_PGOODS)
    },
    showcar({ commit ,state}){
        // console.log(111)
        commit(types.SHOW_CAR)
        
    }
}

var getters = {
    goods(state){
        return state.goods
    },
    showpgs(state){
        return state.showpgs
    },
    food(state){
        return state.food
    },
    showcar(state){
        console.log('asdadada')
        return state.showcar  
    },
    closepgoods(state){
        return state.closepgoods
    }
}   

var mutations = {
    [types.GET_GOODS](state,data){
        console.log(data)
        state.goods = data
    },
    [types.SHOW_PARTGOODS](state,data){
        
        
        state.showpgs = true
        state.food = data;
        // console.log(data)
        console.log(state.showpgs)
        
    },
    [types.SHOW_CAR](state,data){
        state.showcar = !state.showcar
        console.log(11111)
    },
    [types.CLOSE_PGOODS](state,data){
        state.showpgs = false
    }
}

export default {
    state,
    actions,
    getters,
    mutations
}


